                <div className="mb-2 md:mb-6 text-secondary p-2 md:p-4 bg-white/5 rounded-full ring-1 ring-white/20 shadow-[0_0_15px_rgba(250,204,21,0.2)] scale-75 md:scale-100">{stat.icon}</div>
